import Home from './Home'
import Doc from './Doc'
import Teach from './Teach'
import Blog from './Blog'
import Community from './Community'
import Login from './Login'
import Core from './Core'
import Guide from './Guide'
import Api from './Api'
import Hooks from './Hooks'


const routes = [
  {
    path:'/',
    exact:true,
    component:Home
  },
  {
    path:'/teach',
    component:Teach,
    pri:true
  },
  {
    path:'/community',
    component:Community
  },
  {
    path:'/blog',
    component:Blog
  },
  {
    path:'/login',
    component:Login
  },

  {
    path:'/doc',
    component:Doc,
    pri:true,
    routes:[
      {
        path:'/doc/core',
        component:Core,
      },
      {
        path:'/doc/guide',
        component:Guide,
      },
      {
        path:'/doc/api',
        component:Api,
      },
      {
        path:'/doc/hooks',
        component:Hooks,
      }
    ]
  },
]

export default routes;