import React from 'react'
import { BrowserRouter as Router, Route, NavLink, Switch, Redirect } from 'react-router-dom'
import './App.css'
import routes from './router.config'


function PrivateRoute({ component: Com, ...rest }) {
  return (
    <Route
      {...rest}
      render={({ location }) =>
        localStorage.getItem('userinfo') ? <Com  {...rest} /> : (
          <Redirect to={{ pathname: "/login", state: location }} />
        )
      }
    />
  );
}

function RouteWithSubRoutes(route) {
  if (route.pri) {
    console.log(route);
    return (
      <PrivateRoute
        path={route.path}
        component={route.component}
        routes={route.routes}
      />
    );
  }
  return (
    <Route
      path={route.path}
      component={route.component}
      routes={route.routes}
      exact={route.exact}
    />
  );
}

const App = () => {
  return <Router>
    <header>
      <nav>
        <NavLink activeClassName="active" exact to='/'>React</NavLink>
        <NavLink activeClassName="active" to='/doc'>文档</NavLink>
        <NavLink activeClassName="active" to='/teach'>教程</NavLink>
        <NavLink activeClassName="active" to='/blog'>博客</NavLink>
        <NavLink activeClassName="active" to='/community'>社区</NavLink>
      </nav>
    </header>
    <Switch>
      {
        routes.map((route, i) => (
          <RouteWithSubRoutes key={i} {...route} />
        ))
      }
    </Switch>
  </Router>
}

export { App, RouteWithSubRoutes };
